body {
  margin: 0;

 align-content: center;
 
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
  width: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
html{
  scroll-behavior: smooth;
  height: 100%;
}
@media only screen and (min-width: 360px) { 
  body{
    margin-top: 0;
    margin-bottom: 0;
    margin-left: auto;
    margin-right: auto;
    width: 100%;
    display:inline-block;
  }
}

 .topnav  {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.3);
  height: 50px;
  } 
  .iconholder .navheading{  
     
      color: #86A2D1;
      position: absolute;
      top:12px;
      left: 45px;
      font-size: 20px;
      font-style: italic;
      text-align: center;
      flex-wrap: wrap;
      font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  }
  .topnav ul { 
    display: inline;
    margin: 0;
    list-style: none;  
    margin-right: 8px;
    position: relative;
    right: 7px;
  }
 .topnav ul li {   
      display: inline;
      opacity: 0.8;
      padding: 0 30px 20px 30px;
      font-size: 15px;
      font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  }
.topnav ul li a {
  text-decoration: none;
  text-align: right;
  color: #fff;
  margin: 10px;
}
.topnav  ul li a,
.topnav  ul li a:after,
.topnav  ul li a:before {
  transition: all .5s;
 
}
.topnav  ul li a:hover {
  color: #E3B120;
  
}


  .icon {
    padding-left: 10px;
    padding-right: 10px;
    flex: 1 1;
  }
  .iconholder {
    display: flex;
    position: 500px;  
    display: flex;
    padding-right: 20px;
}
 .navbar-toggle {
  position: absolute;
  top: 10px;
  right: 20px;
  cursor: pointer; 
  color: rgba(255,255,255,0.8);
  font-size: 24px;
  display: none;
}

@media only screen and (max-width: 360px) { 
  .iconholder .navheading{ 
    padding-left: 13px; 
  }
}

  @media only screen and (max-width: 1000px) {
    .iconholder {
      height: 10%;
    } 
    .getNavBar{
      margin-right: 0;
      margin-left: 0;
    }  
    .topnav ul{
      display: none;
    }
    .topnav  li,
    .topnav   li a:hover {
     visibility: hidden;
  }
}
.button {
    height: 24px;
    width: 30px;
    margin: 5px;
    background: transparent;
    justify-content: space-around;
    border: none;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    padding: 0;
    box-sizing: border-box;
    position: fixed;
    top: 7px;
    z-index: 1000;
}
.button:focus {
    outline: none;
}
.button-line {
   width: 30px;
   height: 2px;
   background: #E3B120;
}
